
import * as ContentHtml from './ContentHtml';
import * as ContentPlain from './ContentPlain';

export function render(format, content) {
   if (format === 'html') {
      ContentHtml.render(content);
   } else {
      ContentPlain.render(content);
   }
}

export function assignDeps(g) {
   g.Ch = ContentHtml;
   g.Cp = ContentPlain;
}
